<template>
  <div class="split2 left">
    <complaint-form
      ref="complaintForm"
      :allButtonsDisabled="vidPlaying"
      @selectDevice="selectDevice($event)"
      @deselectDevice="deselectDevice($event)"
      @openDevice="openDevice($event)"
      @closeDevice="closeDevice($event)"
      @selectDrawer="selectDrawer($event)"
      @selectPlate="selectPlate($event)"
      @selectPlateSize="selectPlateSize($event)"
    >
    </complaint-form>

    <div class="centered hand">
      
      <video id="gestureVid">
        <source
          id="gestureSource"
          src="https://raw.githubusercontent.com/dubbye/aal/main/gestures/init_schub.mp4"
          type="video/mp4"
        />
      </video>
      <video id="gestureVidBackground">
        <source
          id="gestureVidBackgroundSrc"
          src="https://raw.githubusercontent.com/dubbye/aal/main/gestures/init_schub.mp4"
          type="video/mp4"
        />
      </video>

    </div>
  </div>

  <div class="split right">
    <div class="centered">
      <video id="kitchenVid">
        <source
          id="kitchenSource"
          src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Fridge_Fokus.mp4"
          type="video/mp4"
        />
      </video>
      <img
        id="kitchenVidBackground"
        src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Kitchen.png"
      />
    </div>
  </div>
</template>

<script>
import ComplaintForm from "./components/ComplaintForm.vue";

export default {
  name: "App",
  components: {
    ComplaintForm,
  },
  data() {
    return {
      vidPlaying: false,
      selectedPlate: 0,
      deviceOpen: false,
      selectedDrawer: 0,
      selectedPlateSize: '',
    };
  },
  methods: {
    selectDevice(device) {
      var gestureVidSource = "";
      var kitchenVidSource;
      document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Kitchen.png";

      if (device === "Schublade") {
        gestureVidSource =
          "https://raw.githubusercontent.com/dubbye/aal/main/gestures/init_schub.mp4";
        kitchenVidSource =
          "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Schubladen/Schublade_Fokus.mp4";
      } else if (device === "Fridge") {
        gestureVidSource =
          "https://raw.githubusercontent.com/dubbye/aal/main/gestures/init_kühl.mp4";
        kitchenVidSource =
          "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Fridge_Fokus.mp4";
      } else {
        gestureVidSource =
          "https://raw.githubusercontent.com/dubbye/aal/main/gestures/init_herd.mp4";
        kitchenVidSource =
          "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Herd/Herd_Fokus.mp4";
      }
      this.playGestureVid(gestureVidSource, kitchenVidSource);
    },
    deselectDevice(device) {
      var gestureVidSource =
        "https://raw.githubusercontent.com/dubbye/aal/main/gestures/exit.mp4";
      var kitchenVidSource;
      //deselect specific plate bzw drawer und das bei close sowie reset bei kühlscchrnak und drawer das gleiche passiert
      if (device === "Schublade") {
        if (this.deviceOpen) {
          kitchenVidSource =
            "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Schubladen/Schublade"+this.selectedDrawer+"_Reset.mp4";
          document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Schubladen/Schublade"+this.selectedDrawer+"_Open.png";
        }else{
          kitchenVidSource =
            "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Schubladen/Schublade_Fokus_Reset.mp4";
          document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Schubladen/Schublade_Reset+Fokus.png";
        }
      } else if (device === "Fridge") {
        if (this.deviceOpen) {
          kitchenVidSource =
            "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Fridge_Reset.mp4";
          document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Fridge_Reset.png";
        }else{
          kitchenVidSource =
            "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Fridge_Fokus_Reset.mp4";
          document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Fridge_Door_Open_R.png";
        }          
      } else {
        if (this.deviceOpen) {
          kitchenVidSource =
            "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Herd/HerdPlatte"+this.selectedPlate+"_"+this.selectedPlateSize+"_Reset.mp4";
          document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Herd/HerdPlatte"+this.selectedPlate+"_An_"+this.selectedPlateSize+".png";
        }else{
          kitchenVidSource =
            "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Herd/HerdPlatte_Fokus_Reset.mp4";
          document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Herd/HerdPlatte_Fokus_Reset.png";
        }          
      }
      this.deviceOpen = false;
      this.selectedPlate = 0;
      this.selectedPlateSize = '';
      this.selectedDrawer = 0;
      this.playGestureVid(gestureVidSource, kitchenVidSource);
    },
    openDevice(device_num) {
      this.deviceOpen = true;
      var gestureVidSource =
        "https://raw.githubusercontent.com/dubbye/aal/main/gestures/auf.mp4";
      var kitchenVidSource = "";
      if (device_num === 0) {
        document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Fridge_Door_Open_R.png";
        kitchenVidSource =
          "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Fridge_Door_Open_R.mp4";
      } else {
        document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Schubladen/Schublade_Reset+Fokus.png";
        kitchenVidSource =
          "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Schubladen/Schublade" +
          device_num +
          "_Open.mp4";
      }
      this.playGestureVid(gestureVidSource, kitchenVidSource);
    },
    closeDevice(device_num) {
      var gestureVidSource =
        "https://raw.githubusercontent.com/dubbye/aal/main/gestures/zu.mp4";
      var kitchenVidSource;

      if (device_num === 0) {
        document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Fridge_Reset.png";
        kitchenVidSource =
          "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Fridge/Fridge_Reset.mp4";
      } else {
        document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Schubladen/Schublade"+this.selectedDrawer+"_Open.png";
        kitchenVidSource =
          "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Schubladen/Schublade" +
          device_num +
          "_Reset.mp4";
      }
      this.deviceOpen = false;
      this.selectedPlate = 0;
      this.selectedPlateSize = '';
      this.selectedDrawer = 0;
      this.playGestureVid(gestureVidSource, kitchenVidSource);
    },
    selectDrawer(num) {
      this.selectedDrawer = num;
      var gestureVidSource =
        "https://raw.githubusercontent.com/dubbye/aal/main/gestures/" +
        num +
        ".mp4";
      document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Schubladen/Schublade_Reset+Fokus.png";
      var kitchenVidSource =         
        "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Schubladen/Schublade" +
        num +
        "_Fokus.mp4";
      this.playGestureVid(gestureVidSource, kitchenVidSource);
    },
    selectPlate(num) {
      this.selectedPlate = num;
      document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Herd/Herdplatte_Fokus.png";

      var gestureVidSource =
        "https://raw.githubusercontent.com/dubbye/aal/main/gestures/" +
        num +
        ".mp4";
      var kitchenVidSource =         
        "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Herd/HerdPlatte" +
        num +
        "_Fokus.mp4";
      this.playGestureVid(gestureVidSource, kitchenVidSource);
    },
    selectPlateSize(size) {
      this.selectedPlateSize = size;
      this.deviceOpen = true;
      document.getElementById('kitchenVidBackground').src="https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Herd/HerdPlatte_Fokus_Selected.png";

      var gestureVidSource =
        "https://raw.githubusercontent.com/dubbye/aal/main/gestures/" +
        size +
        ".mp4";
      var kitchenVidSource =
        "https://raw.githubusercontent.com/dubbye/aal/main/kitchen/Herd/HerdPlatte" +
        this.selectedPlate +
        "_An_" +
        size +
        ".mp4";
      this.playGestureVid(gestureVidSource, kitchenVidSource);
    },
    playGestureVid(gestVid, kitVid) {
      var gestureVidVar = document.getElementById("gestureVid");
      var gestureVidSource = document.getElementById("gestureSource");

      gestureVidSource.src = gestVid;

      gestureVidVar.load();
      gestureVidVar.play();
      this.vidPlaying = true;
      setTimeout(function () {
        var kitchenVid = document.getElementById("kitchenVid");
        var kitchenVidSource = document.getElementById("kitchenSource");
        kitchenVidSource.src = kitVid;
        kitchenVid.load();
        kitchenVid.play();
      }, 1300);
      setTimeout(this.setVidPlay, 3300);
      //this.playKitchenVid(kitVid);
    },
    setVidPlay() {
      this.vidPlaying = false;
    },
  },
};
</script>

