<template>
<div style="width=100px">
  <div class="buttons selection">
    <button type="button" class="btn btn-primary" v-on:click="selectDevice('Schublade'),drawerSelected = true" :disabled="allButtonsDisabled || deviceSelected" style="margin: 10px">Schublade</button>
    <button type="button" class="btn btn-primary" v-on:click="selectDevice('Herd'),stoveSelected = true" :disabled="allButtonsDisabled || deviceSelected" style="margin: 10px">Herd</button>
    <button type="button" class="btn btn-primary" v-on:click="selectDevice('Fridge'),fridgeSelected = true" :disabled="allButtonsDisabled || deviceSelected" style="margin: 10px">Kühlschrank</button>
    <button type="button" class="btn btn-primary" v-on:click="deselectDevice" :disabled="allButtonsDisabled || !deviceSelected || selectedDrawer > 0 && !selectedDeviceOpen || stovePlateSelected && !stovePlateSizeSelected" style="margin: 30px">Gerät verlassen</button>
  </div>

  <div class="buttons drawerNumber" v-if="deviceSelected && drawerSelected">
    <button type="button" class="btn btn-primary" v-on:click="selectDrawer(1)" :disabled="allButtonsDisabled || selectedDrawer > 0" style="margin: 10px">Schublade 1</button>
    <button type="button" class="btn btn-primary" v-on:click="selectDrawer(2)" :disabled="allButtonsDisabled || selectedDrawer > 0" style="margin: 10px">Schublade 2</button>
    <button type="button" class="btn btn-primary" v-on:click="selectDrawer(3)" :disabled="allButtonsDisabled || selectedDrawer > 0" style="margin: 10px">Schublade 3</button>
  </div>

  <div class="buttons openAndClose" v-if="deviceSelected && (selectedDrawer > 0 || fridgeSelected)">
    <button type="button" class="btn btn-primary" v-on:click="openDevice" :disabled="allButtonsDisabled || selectedDeviceOpen" style="margin: 10px">Auf</button>
    <button type="button" class="btn btn-primary" v-on:click="closeDevice" :disabled="allButtonsDisabled || !selectedDeviceOpen" style="margin: 10px">Zu</button>
  </div>

  <div class="buttons plateNumber" v-if="deviceSelected && stoveSelected">
    <button type="button" class="btn btn-primary" v-on:click="selectPlate(1)" :disabled="allButtonsDisabled || stovePlateSelected" style="margin: 10px">Platte 1</button>
    <button type="button" class="btn btn-primary" v-on:click="selectPlate(2)" :disabled="allButtonsDisabled || stovePlateSelected" style="margin: 10px">Platte 2</button>
    <button type="button" class="btn btn-primary" v-on:click="selectPlate(3)" :disabled="allButtonsDisabled || stovePlateSelected" style="margin: 10px">Platte 3</button>
    <button type="button" class="btn btn-primary" v-on:click="selectPlate(4)" :disabled="allButtonsDisabled || stovePlateSelected" style="margin: 10px">Platte 4</button>
  </div>
  
  <div class="buttons bigOrSmall" v-if="deviceSelected && stovePlateSelected">
    <button type="button" class="btn btn-primary" v-on:click="selectPlateSize('Klein')" :disabled="allButtonsDisabled || stovePlateSizeSelected" style="margin: 10px">kleine Platte</button>
    <button type="button" class="btn btn-primary" v-on:click="selectPlateSize('Groß')" :disabled="allButtonsDisabled || stovePlateSizeSelected" style="margin: 10px">große Platte</button>
  </div>
</div>



</template>

<script>

export default {
  name: 'ComplaintForm',
  data() {
    return {
      selectedDevice: '',
      deviceSelected: false,
      drawerSelected: false,
      stoveSelected: false,
      fridgeSelected: false,
      stovePlateSelected: false,
      stovePlateSizeSelected: false,
      selectedDeviceOpen: false,
      selectedDrawer: 0,
    };
  },
  emits: {
    selectDevice : null,
    deselectDevice: null,
    openDevice : null,
    closeDevice : null,
    selectPlate: null,
    selectPlateSize: null,
    selectDrawer: null,
  },

  props: {
    allButtonsDisabled: Boolean, //disable all buttons for as long as the video plays
  },

  methods: {

    selectDevice(device){
      this.selectedDevice = device;
      this.deviceSelected = true;
      this.$emit("selectDevice",device)
    },
    deselectDevice(){
      //please dont judge 🙃
      this.deviceSelected = false;
      this.drawerSelected = false;
      this.stoveSelected = false;
      this.fridgeSelected = false;
      this.stovePlateSelected = false;
      this.stovePlateSizeSelected = false;
      this.selectedDeviceOpen = false;
      this.selectedDrawer = 0;

      this.$emit("deselectDevice",this.selectedDevice) 
      this.selectedDevice = ''; // dont think its needed 
    },
    selectDrawer(num){
      this.selectedDrawer = num;
      this.$emit("selectDrawer",num)
    },
    openDevice() {
      this.$emit("openDevice",this.selectedDrawer)
      this.selectedDeviceOpen = true;
    },

    closeDevice() {
      this.$emit("closeDevice",this.selectedDrawer)
      this.deviceSelected = false;
      this.drawerSelected = false;
      this.stoveSelected = false;
      this.fridgeSelected = false;
      this.stovePlateSelected = false;
      this.stovePlateSizeSelected = false;
      this.selectedDeviceOpen = false;
      this.selectedDrawer = 0;
    },

    selectPlate(plateNum) {
      this.stovePlateSelected = true;
      this.$emit("selectPlate",plateNum)
    },

    selectPlateSize(size) {
      this.stovePlateSizeSelected = true;
      this.$emit("selectPlateSize",size)
    },
  }
}
</script>

<style scoped>
#main-card {
  background-color: rgba(114, 19, 19, 0.9);
}
.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

</style>